.full-l {
	width: 100%;
	height: 100vh;
	@include r($md) {
		background-position: 70% 70%; } }
.l {
	max-width: 1170px;
	margin: 0 auto;
	padding: 0 50px;
	@include r($sm) {
		padding: 0 20px; } }
.out {
	margin-bottom: 300px;
	box-shadow: 0 6px 30px rgba(0,0,0,.3);
	position: relative;
	@include r($sm) {
		margin-bottom: 380px; }
	&:before {
		content: "";
		@include coverdiv;
		background-color: $white;
		z-index: -200; } }
.in {
	position: relative;
	z-index: 1; }
.parallax {
	@include coverdiv;
	background-repeat: repeat-y;
	background-position: 0% 50%;
	background-size: contain;
	z-index: 0; }
