body {
	@include u;
	line-height: 1.4;
	color: $common;
	//background: $gradient-simple
	background-color: $white;
	position: relative;
	overflow-x: hidden;
	&.is-fixed {
		overflow: hidden;
		max-height: 100vh; } }
.btn {
	border: 0;
	outline: 0;
	display: inline-block;
	position: relative;
	overflow: hidden;
	font-size: 14px;
	@include u;
	&_ts {
		background-color: transparent; }
	&_link {
		text-decoration: none; }
	&_yellow {
		background-color: $yellow;
		border-radius: 4px;
		color: $gray;
		padding: 15px 25px; } }

.gradient-bg {
	background: $gradient-yellow; }
.gradient-bg-reverse {
	background: $gradient-yellow-reverse; }

.section {
	position: relative;
	&__in {
		padding: 50px 0; }
	&__title {
		text-align: center;
		margin-bottom: 40px;
		color: $violet;
		font-size: 18px;
		&_white {
			color: $white;
			h2 {
				font-weight: normal; } } } }
.map {
	width: 100%;
	&__canvas {
		width: 100%;
		height: 450px; } }

.section {
	overflow: hidden; }
