.start {
	width: 100%;
	height: 100%;
	position: relative;
	background-color: transparent;
	//+image('anykey-start.jpg')
	@include cover;
	&:before {
		content: "";
		@include coverdiv;
		z-index: -1;
		background-color: $white; }

	&__in {
		@include vcenter;
		min-width: 80%;
		text-align: center; }
	&__title {
		font-size: 48px;
		color: $violet;
		padding: 10vh 0;
		@include r($md) {
			font-size: 36px; }
		&_white {
			color: $white; } }
	&__btn {
		text-align: center;
		.btn {
			animation: scroll infinite 2s ease-out;
			&:hover {
				animation-play-state: paused; } }
		.icon-arrow {
			font-size: 72px;
			fill: $white;
			@include r($md) {
				font-size: 54px; } } } }


